html {
  font-size: 100%;
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  padding: 0;
  font: var(--font);
  min-height: 150vh;
}

.visually-hidden {
  position: absolute;
  left: -10000px;
  top: auto;
  width: 1px;
  height: 1px;
  overflow: hidden;
}

h1,
h2,
h3 {
  margin-top: 0;
}

a,
a:visited,
a:active {
  text-decoration: none;
}

:root {
  --limegreen: hsl(163, 72%, 41%);
  --lightpurple: hsl(243, 78%, 82%);
  --hotpink: hsl(338, 100%, 69%);
  --offsetblue: hsl(240, 100%, 73%);
}

:root {
  --font: "...", ;
}